import { React, useState } from "react";
import { FaPlusCircle } from "react-icons/fa";

const SelectShop = ({ shops, setShops }) => {
  const [openShopOptions, setOpenShopOptions] = useState(true);
  // const [shops, setShops] = useState(state);

  const saveShopChoiceFunction = (index) => {
    //setSaveShopChoice([...saveShopChoice, { name, image, id }]); <-------- local

    const newShops = shops;
    newShops[index].selected = !newShops[index].selected;
    setShops([...newShops]);
    setOpenShopOptions(!openShopOptions);
  };

  return (
    <div>
      <div
        className="container"
        onClick={() => setOpenShopOptions(!openShopOptions)}
      >
        <p className="add-shop">Select a shop </p>
        <FaPlusCircle className="add add-shop" />
      </div>
      <div
        className={`shop-container ${
          openShopOptions ? "show-shop-container" : "hide-shop-container"
        }`}
      >
        {shops.map((shop, index) => {
          const { id, name, image } = shop;
          return (
            <>
              <img
                className="shop-image"
                key={id}
                src={image}
                alt={`select ${name}`}
                onClick={() => saveShopChoiceFunction(index)}
              />
            </>
          );
        })}
      </div>
    </div>
  );
};

export default SelectShop;
