import React from "react";
import { FaTrash } from "react-icons/fa";

const RemoveShop = ({ setShops, shops, index }) => {
  const removeShop = (index) => {
    const newShops = shops;
    newShops[index].selected = !newShops[index].selected;
    setShops([...newShops]);
  };

  return (
    <div className="remove-shop-container" onClick={() => removeShop(index)}>
      <p className="remove-shop-wording"> Remove Shop </p>
      <FaTrash className=" react-icons" />
    </div>
  );
};

export default RemoveShop;
