import { React, useState, useEffect } from "react";

const TotalItems = ({ shops }) => {
  const [totalItemCount, setTotalItemCount] = useState(0);

  useEffect(() => {
    const totalItemsRemaining =
      shops[0].listLength +
      shops[1].listLength +
      shops[2].listLength +
      shops[3].listLength +
      shops[4].listLength +
      shops[5].listLength;
    //setTotalItemCount(totalItemsRemaining);
  }, [shops]);

  return (
    <div>
      {totalItemCount > 0 && (
        <div className="total-items">
          {totalItemCount > 1
            ? `${totalItemCount} items left`
            : `${totalItemCount} item left`}
        </div>
      )}
    </div>
  );
};

export default TotalItems;
