import React from "react";
import { FaTrash } from "react-icons/fa";

const RemoveAllItems = ({ setShops, shop }) => {
  //console.log(shop.id);
  const removeAllShopItems = (id) => {
    setShops((shops) => {
      return shops.reduce((acc, shop) => {
        if (shop.id === id) {
          return [...acc, { ...shop, list: [] }];
        }
        return [...acc, shop];
      }, []);
    });
  };

  return (
    <div
      className="remove-all-items-container"
      onClick={() => removeAllShopItems(shop.id)}
    >
      <p className="remove-all-items-wording"> Remove Items </p>
      <FaTrash className=" react-icons" />
    </div>
  );
};

export default RemoveAllItems;
