import { React, useState } from "react";
import ListContainer from "./Body/ListContainer";
import SelectShop from "./Body/SelectShop";
import Footer from "./Footer/Footer";
import Header from "./Header/Header";
import state from "./State/State";

function App() {
  const [shops, setShops] = useState(state);

  return (
    <>
      <Header />
      <SelectShop shops={shops} setShops={setShops} />
      <ListContainer shops={shops} setShops={setShops} />
      <Footer />
    </>
  );
}

export default App;
