import { React } from "react";
//import { v4 as uuidv4 } from "uuid"; <---- local
import AddItem from "./AddItem";
import TotalItems from "./TotalItems";

const ListContainer = ({ shops, setShops }) => {
  // const [message, setMessage] = useState({});

  return (
    <div>
      <TotalItems shops={shops} />
      {shops.map((shop, index) => {
        if (shop.selected === true) {
          return (
            <AddItem
              key={shop.id}
              shop={shop}
              shops={shops}
              setShops={setShops}
              index={index}
            />
          );
        }
        return null;
      })}
    </div>
  );
};

export default ListContainer;
