import { React, useState } from "react";
import { FaEllipsisH } from "react-icons/fa";
import { v4 as uuidv4 } from "uuid";
import ItemInput from "./ItemInput";
import ListItems from "./ListItems";
import RemoveAllItems from "./RemoveAllItems";
import RemoveShop from "./RemoveShop";

const AddItem = ({ shop, shops, setShops, index }) => {
  const [removeState, setRemoveState] = useState(false);

  return (
    <div key={shop.id} className="shop-card">
      <div className="shop-card-header-container">
        <img src={shop.image} alt={shop.name} className="selected-shop-image" />
        <FaEllipsisH
          className="react-icons"
          onClick={() => setRemoveState(!removeState)}
        />
      </div>
      {shop.list.length > 0 && (
        <div className="shop-items-remaining">
          {shop.list.length > 1
            ? `${shop.name} (${shop.list.length} items left)`
            : `${shop.name} (${shop.list.length} item left)`}
        </div>
      )}
      <ItemInput setShops={setShops} shop={shop} index={index} />
      {removeState && (
        <div className="remove-container">
          <RemoveShop shops={shops} setShops={setShops} index={index} />
          <RemoveAllItems setShops={setShops} shop={shop} />
        </div>
      )}
      <ListItems shops={shops} setShops={setShops} shop={shop} index={index} />
    </div>
  );
};

export default AddItem;
